/* This stylesheet is used to style the public-facing components of the plugin. */
.Grid {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}
.Grid * {
  box-sizing: border-box;
}
.Grid-cell {
  flex: 1;
}
.Grid--flexCells > .Grid-cell {
  display: flex;
}
.Grid--top {
  align-items: flex-start;
}
.Grid--bottom {
  align-items: flex-end;
}
.Grid--fill-height {
  flex-direction: column;
  justify-content: space-between;
}
.Grid--nowrap {
  flex-wrap: nowrap;
}
.Grid--center {
  align-items: center;
}
.Grid--justifyCenter {
  justify-content: center;
}
.Grid-cell--top {
  align-self: flex-start;
}
.Grid-cell--bottom {
  align-self: flex-end;
}
.Grid-cell--center {
  align-self: center;
}
.Grid-cell--autoSize {
  flex: none;
}
.Grid--fit > .Grid-cell {
  flex: 1;
}
.Grid--full > .Grid-cell {
  flex: 0 0 100%;
}
.Grid--1of2 > .Grid-cell {
  flex: 0 0 auto;
  width: 50%;
}
.Grid--1of3 > .Grid-cell {
  flex: 0 0 auto;
  width: 33.3%;
}
.Grid--1of4 > .Grid-cell {
  flex: 0 0 auto;
  width: 25%;
}
.Grid-cell .inset-contents {
  height: 100%;
  padding: 0.8em 1em 0px;
}
.Grid-cell > .Grid-cell-100pc {
  width: 100%;
}
@media (min-width: 24em) {
  .small-Grid--fit > .Grid-cell {
    flex: 1;
  }
  .small-Grid--full > .Grid-cell {
    flex: 0 0 100%;
  }
  .small-Grid--1of2 > .Grid-cell {
    flex: 0 0 auto;
    width: 50%;
  }
  .small-Grid--1of3 > .Grid-cell {
    flex: 0 0 auto;
    width: 33.3%;
  }
  .small-Grid--1of3 > .Grid-cell.u-2of3 {
    flex: 0 0 auto;
    width: 66.6%;
  }
  .small-Grid--1of4 > .Grid-cell {
    flex: 0 0 auto;
    width: 25%;
  }
  .small-Grid--1of4 > .Grid-cell.u-2of4 {
    flex: 0 0 auto;
    width: 50%;
  }
  .small-Grid--1of4 > .Grid-cell.u-3of4 {
    flex: 0 0 auto;
    width: 75%;
  }
}
@media (min-width: 36em) {
  .med-Grid--fit > .Grid-cell {
    flex: 1;
  }
  .med-Grid--full > .Grid-cell {
    flex: 0 0 100%;
  }
  .med-Grid--1of2 > .Grid-cell {
    flex: 0 0 auto;
    width: 50%;
  }
  .med-Grid--1of3 > .Grid-cell {
    flex: 0 0 auto;
    width: 33.3%;
  }
  .med-Grid--1of3 > .Grid-cell.u-2of3 {
    flex: 0 0 auto;
    width: 66.6%;
  }
  .med-Grid--1of4 > .Grid-cell {
    flex: 0 0 auto;
    width: 25%;
  }
  .med-Grid--1of4 > .Grid-cell.u-2of4 {
    flex: 0 0 auto;
    width: 50%;
  }
  .med-Grid--1of4 > .Grid-cell.u-3of4 {
    flex: 0 0 auto;
    width: 75%;
  }
  .large-Grid--1of4 > .Grid-cell {
    flex: 0 0 auto;
    width: 50%;
  }
  .large-Grid--1of4 > .Grid-cell img {
    width: 100%;
  }
}
@media (min-width: 48em) {
  .large-Grid--fit > .Grid-cell {
    flex: 1;
  }
  .large-Grid--full > .Grid-cell {
    flex: 0 0 100%;
  }
  .large-Grid--1of2 > .Grid-cell {
