.ie7 .Grid,
.ie8 .Grid,
.ie9 .Grid {
  display: block;
}
.ie7 .Grid-cell,
.ie8 .Grid-cell,
.ie9 .Grid-cell {
  display: block;
  float: left;
  width: 30%;
  margin: 0 0 1em 1em;
}
.ie7 .large-Grid--1of4 .Grid-cell,
.ie8 .large-Grid--1of4 .Grid-cell,
.ie9 .large-Grid--1of4 .Grid-cell {
  margin-right: 2%;
  margin-left: 2%;
  width: 20.9%;
}
.ie7 .Grid--fill-height .Grid-cell,
.ie8 .Grid--fill-height .Grid-cell,
.ie9 .Grid--fill-height .Grid-cell {
  width: 100%;
  clear: left;
}
body.colors-dark,
.colors-dark .navigation-top,
.colors-dark .main-navigation ul {
  background: #2c2929;
}
.some-cool-class {
  font-size: 56px;
  font-size: 4rem;
}
.vis-container {
  padding: 1.5em;
  margin-bottom: 2em;
  border-radius: .2em;
  background-color: rgba(130, 119, 114, 0.15);
}
.colors-dark .axis path.domain,
.colors-dark .axis .tick line {
  stroke: white;
}
.colors-dark .vis-container text {
  fill: white;
}
.data-set-edit td,
.data-set-edit th {
  border: 1px solid #333;
}
.data-set-edit td:first-child,
.data-set-edit th:first-child {
  padding-left: 0.4em;
}
.data-set-edit th {
  border-bottom: 1px solid #666;
}
.data-set-edit .hidden {
  display: none;
}
.data-set-edit .minimal {
  background: transparent;
  padding: 0;
}
.data-set-edit .row-actions .dashicons {
  line-height: 1.3;
}
.data-set-edit .row-actions .remove-row {
  margin-left: .4em;
}
.data-set-edit .row-actions .dashicons-no {
  color: crimson;
}
.data-set-edit .row-actions .dashicons-move {
  color: #eeeeee;
}
.data-set-edit-container .table-action {
  padding: .6em 1.2em;
}
.data-set-edit-container .table-action .dashicons {
  line-height: 1.3;
}
.data-set-edit-container .table-action .dashicons-plus {
  color: green;
}
